<script>
    export default {
        name: "History",
        data(){
            return {
                user_id:"",
                limit:20,
                offset:0,
                hbHistory1:"",
                hbHistory2:""
            }
        },
        created(){
            this.user_id = JSON.parse(localStorage.getItem('user_id'));
            // console.log(this.user_id);
            this.axios.get("/promotion/v2/users/" + this.user_id + "/expired_hongbaos",{
                params:{
                    limit:this.limit,
                    offset:this.offset
                }
            }).then(res=>{
                console.log(res);
                // this.hbHistory = res;
                this.hbHistory1 = res.filter(item => item.limit_map)
                this.hbHistory2 = res.filter(item => item.limit_map == undefined)
                console.log(this.hbHistory1);
            }).then(err=>{});
        }
    }
</script>

